<template>
    <section class="logbox">
        <div class="username">
            <input type="text" placeholder="username" v-model="username" />
        </div>
        <div class="password">
            <input type="password" placeholder="password" v-model="password" />
        </div>
        <a href="#" class="btn" @click='reg'>SignUp</a>
        <a href="#" class="log" @click='sw'>Login</a>
    </section>
</template>

<script>
    import {register} from '@/network/api.js'

    export default {
        name: 'subreg',
        data() {
            return {
                username: "",
                password: "",
                result: "",
            }
        },
        methods: {
            sw() {
                this.$emit('change')
            },
            // 调用注册函数
            async reg() {
                this.$store.commit('setLoading')
                let abc = await register(this.username, this.password)
                if(abc) {
                    this.$emit('change', {
                        name: this.username,
                        code: this.password
                    })
                }
                this.$store.commit('setLoading')
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .logbox {
        margin-top: 70px;
        display: flex;
        flex-wrap: wrap;
        height: 200px;
        overflow: hidden;
    }

    .username {
        background-image: url('../img/p.png');
        background-repeat: no-repeat;
        padding-left: 20px;
    }

    .password {
        background-image: url('../img/password.png');
        background-repeat: no-repeat;
        padding-left: 20px;
    }

    input {
        outline: none;
        border-left: none;
        border-top: none;
        border-right: none;
        border-bottom: #981298 1px solid;
        background-color: transparent;
        color: black;
    }

    ::-webkit-input-placeholder {
        color: black;
    }

    .btn {
        display: block;
        width: 100%;
        height: 30px;
        padding-top: 8px;
        background-color: #833993;
        text-decoration: none;
        color: whitesmoke;
        text-align: center;
        border-radius: 10px;
    }

    .log {
        margin-top: -25px;
        display: block;
        text-decoration: none;
        text-align: center;
        width: 100%;
        color: gray;
        font-size: 12px;
    }
</style>