<template>
  <section class="logbox">
    <div class="username">
      <input type="text" placeholder="username" v-model="username" autocomplete="off"/>
    </div>
    <div class="password">
      <input type="password" placeholder="password" v-model="password" autocomplete="off"/>
    </div>
    <div v-if="typeof person.name !== 'undefined'" class="tip">
      Account created
    </div>
    <div v-if="incorrect" class="tip" style="color: red">
      incorrect username or password
    </div>
    <a href="#" class="btn" @click="logfn">Login</a>
    <a href="#" class="snu" @click="sw">SignUp</a>
  </section>
</template>

<script>
import { loginfunction } from "@/network/api.js";

export default {
  name: "sublogin",
  props: ["person"],
  data() {
    return {
      incorrect: false,
      username: "",
      password: "",
    };
  },
  methods: {
    sw() {
      this.incorrect = false;
      this.$emit("change");
    },

    async logfn() {
      let value;
      try {
        if (this.username !== "" && this.password !== "") {
          this.$store.commit('setLoading')
          value = await loginfunction(this.username, this.password);
          sessionStorage.setItem("username", value.username);
          sessionStorage.setItem("id", value.id);
          sessionStorage.setItem("token", value.token);
          this.$store.commit("setUser");
          this.$store.commit("setBg");
          this.$store.commit('setLoading')
          this.$router.replace({
            path: "/home",
          });
        } else {
          this.incorrect = true;
        }
      } catch (error) {
        this.$store.state.loadshow = false
        this.incorrect = true;
      }
    },
  },
};
</script>

