import store from "../store";

export const webonline = () => {
  let img = new Image();
  img.src = 'https://lh3.googleusercontent.com/kPZzK2-bZOssxxI3us3MmJZumwwbX_BhsR9Rs4M0CWnRkCwCl-JsaIS7uRL7Csl28mg=s180-rw' + '?' + Math.random()
  let timer = setTimeout(() => {
    img.onerror()
  }, 2000)
  img.onload = function() {
    store.state.website = false
    clearTimeout(timer)
    timer = null
    console.log('可以翻墙')
  }
  img.onerror = function() {
    store.state.website = true
    console.log('禁止翻墙')
    clearTimeout(timer)
    timer = null
  }
}
