import Vue from 'vue'
import VueRouter from 'vue-router'

import About from '../views/About.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'About',
    component: About
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue'),
    redirect: '/home/subscribe',
    children: [
      {
        path: 'subscribe',
        name: 'Subscribe',
        component: () => import(/* webpackChunkName: "subscribe" */ '../views/subviews/Subscribe.vue')
      },
      {
        path: 'search',
        name: 'Search',
        component: () => import(/* webpackChunkName: "search" */ '../views/subviews/Search.vue')
      },
      {
        path: 'upload',
        name: 'Upload',
        component: () => import(/* webpackChunkName: "upload" */ '../views/subviews/Upload.vue')
      },
      {
        path: 'profile',
        name: 'Profile',
        component: () => import(/* webpackChunkName: "profile" */ '../views/subviews/Profile.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if(to.name !== 'About' && sessionStorage.getItem('id') == null) next({name: 'About'})
  next()
})

export default router
