import axios from 'axios'

axios.defaults.baseURL = 'https://lit-depths-01809.herokuapp.com'

// axios.interceptors.request.use(data => {
//     store.commit('setLoading')
//     return data
// })

axios.interceptors.response.use((response) => {
    return response.data
}, (error) => {
    return Promise.reject(error)
})

// 注册函数
export const register = (username, password) => {
    let obj = JSON.stringify({
        username: username,
        password: password,
    })
    return axios.post("/user/register", obj, {
        headers: {
            'Content-Type': 'application/json'
        }
    })
}

export const loginfunction = (username, password) => {
    let obj = JSON.stringify({
        username: username,
        password: password,
    })
    return axios.post("/user/login", obj, {
        headers: {
            'Content-Type': 'application/json'
        }
    })
}

// upload picture

export const upload = (formdata, token) => {
    return axios.post("/post/upload", formdata, {
        headers: {
            'Authorization': `Bearer ${token}` 
        }
    })
}

// profile get username and detail
export const getUser = (token) => {
    return axios.get('/user/self/', {
        headers: { 'Authorization': `Bearer ${token}` }
    })
}

// profile set user picture
export const setImage = (formdata, token) => {
    return axios.patch('/user/update', formdata, {
        headers: { 'Authorization': `Bearer ${token}` }
    })
}

// profile get current user posts
export const getUserPosts = (username, from, to) => {
    return axios.get(`/post/profile/${username}/${from}/${to}`)
}

// get all other's posts
export const getAllPosts = (username, from, to) => {
    return axios.get(`/post/explore/${username}/${from}/${to}`)
}

// like or unlike post
export const likepost = (url, _id, token) => {
    return axios.patch(`${url}`, JSON.stringify({
        id: _id
    }), {
        headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
        }
    })
}

// get all subscriber
export const subscriberpost = (username) => {
    return axios.get(`/post/feed/${username}/0/30`)
}

// set block/follow
export const bf = (url, username, token) => {
    let obj = JSON.stringify({
        id: null,
        username: username,
    })
    return axios.patch(`${url}`, obj, {
        headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
        }
    })
}







