import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        username: sessionStorage.getItem('username'),
        id: sessionStorage.getItem('id'),
        token: sessionStorage.getItem('token'),
        linear: sessionStorage.getItem('linear'),
        loadshow: false,
        website: false,
        // targetUser: "",
    },
    mutations: {
        setUser() {
            this.state.username = sessionStorage.getItem('username')
            this.state.id = sessionStorage.getItem('id')
            this.state.token = sessionStorage.getItem('token')
            // this.state.token = sessionStorage.getItem('targetUser')
        },
        setBg() {
            sessionStorage.setItem('linear', false)
            this.state.linear = false
        },
        setLoading() {
            this.state.loadshow = !this.state.loadshow
        }
    },
    actions: {
    },
    modules: {
    }
})
