<template>
  <div class="about">
    <div class="title">
      <img class="loginpic" src="../img/login.png" />
    </div>
    <Load-com v-show="$store.state.loadshow"></Load-com>
    <div v-show="!$store.state.loadshow && !$store.state.website">
      <transition name="slide-fade">
        <sub-login v-show="show" @change="sw" :person="user"></sub-login>
      </transition>
      <transition name="slide-fade">
        <sub-reg v-show="!show" @change="sw"></sub-reg>
      </transition>
    </div>
    <div v-show="$store.state.website">检测到您未连上VPN，由于图片管理器需要翻墙，请使用VPN上网</div>
  </div>
</template>

<script>
import sublogin from "@/components/sublogin";
import subreg from "@/components/subreg";
import {webonline} from '@/network/webwall.js'

export default {
  data() {
    return {
      show: true,
      user: {},
    };
  },
  components: {
    "sub-login": sublogin,
    "sub-reg": subreg,
  },
  methods: {
    sw(user) {
      this.show = !this.show;
      if (user) {
        this.user = user;
      }
    },
  },
  beforeCreate() {
    if (sessionStorage.getItem("id") !== null) {
      this.$router.replace({
        path: "/home",
      });
    }
  },
  mounted() {
    webonline()
  }
};
</script>

<style scoped>
.about {
  padding: 40px;
}

.title {
  margin-top: 40px;
}

.loginpic {
  width: 200px;
  height: 40px;
}

.slide-fade {
  position: absolute;
  left: 0;
  right: 0;
}
.slide-fade-enter-active {
  transition: all 1.2s ease;
}
.slide-fade-leave-active {
  transition: all 0.1s cubic-bezier(2, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  left: 0;
  right: 0;
  transform: translateX(50px);
  opacity: 0;
}
</style>
